@import "./sanitize.css";
@import "./forms.css";
@import "./assets.css";
@import "./typography.css";
@import "./reduce-motion.css";

hr {
  height: 0;
  border: 0;
  border-top: var(--sizing-border-width) solid var(--color-border);
  color: inherit;
}

[hidden],
template {
  display: none !important;
}

canvas {
  display: inline-block;
}
